.main-inner-container__title {
  width: 280px;
  margin: 0 auto;
  margin-top: 25px;
  margin-bottom: 42px;
  font-family: $title-font;

  @include text-size(36px, 36px, 400);
}

.main-inner-container__description {
  width: 280px;
  margin: 0 auto;
  margin-top: -4px;
  margin-bottom: 39px;
  font-family: $title-font;
  text-transform: uppercase;

  @include text-size(14px, 18px, 400);
}

@media (min-width: $tablet-width) {
  .main-inner-container__title {
    width: 708px;
    margin-top: 65px;
    margin-bottom: 82px;

    @include text-size(60px, 60px, 400);

    &-form {
      width: 630px;
      margin-bottom: 57px;
    }
  }

  .main-inner-container__description {
    width: 708px;

    @include text-size(20px, 30px, 400);

    &-form {
      width: 630px;
      margin-bottom: 66px;
    }
  }
}

@media (min-width: $desktop-width) {
  .main-inner-container__title {
    width: 1220px;
    margin-top: 76px;
  }

  .main-inner-container__description {
    width: 1220px;
  }
}
